<template>
  <div>
    <div v-if="file">
      <h1 class="subtitle title--no-sp is-3">{{ file.name }}</h1>
      <span class="subtitle is-6">
        {{ file.downloadCount }}
        {{ $t("file.downloads") }} / {{ file.visitCount }} {{ $t("file.visits") }}
      </span>
      <div class="mt-6">
        <Adsense data-ad-client="ca-pub-4679085340013866" data-ad-slot="6511749031"></Adsense>
        <div>
          <a
            :href="`${file.url}`"
            class="button is-rounded is-dark mr-4"
            :class="{ 'pointer-events-none': downloaded }"
            target="_blank"
            @click="onFileDownload"
            :disabled="downloaded"
          >{{ $t("file.downloadFile", { num: '1' }) }}</a>
          <a
            :href="`${file.url_s3}`"
            class="button is-rounded is-white"
            :class="{ 'pointer-events-none': downloaded }"
            target="_blank"
            @click="onFileDownload"
            :disabled="downloaded"
          >{{ $t("file.downloadFile", { num: '2' }) }}</a>
        </div>
        <Adsense data-ad-client="ca-pub-4679085340013866" data-ad-slot="6511749031"></Adsense>
      </div>
    </div>
    <div class="mt-6">
      <article class="message max-w-md mr-auto ml-auto">
        <div class="message-header">
          <p class="mb-0">Alibaba Cloud services revenues are nearing a $4
            billion annual run rate.</p>
        </div>
        <div class="message-body">
          The figure and associated growth rates suggests the China-based technology
          giant may eventually leapfrog Google Cloud Platform (GCP) revenues while pursuing
          Amazon Web Services (AWS) and Microsoft Azure for global public cloud dominance.
          Alibaba cloud revenues were US$962 million for the quarter ended December 31, 2018 —
          up a robust 84 percent compared to the corresponding quarter in 2017, the company
          disclosed this morning. At that growth rate, Alibaba could be positioned to leapfrog
          Google Cloud Platform (GCP) annual revenues sometime between 2019 and 2021,
          ChannelE2E suspects.
          (Note: ChannelE2E will update actual cloud revenue figures/estimates from AWS,
          Azure and GCP as each company discloses quarterly financial results in the days
           ahead. We’ll potentially mix IBM into the conversation as well. Stay tuned.)
          Alibaba Cloud: What’s Driving Growth?
          The company is gaining momentum with enterprise customers, Alibaba indicated.
          Moreover, the company launched 678 new cloud-related features during the quarter —
          addressing such areas as data intelligence, AI applications, security and
          enterprise solutions.
          Alibaba has also aligned its cloud services executive team with the company’s
          overall technology organization. Jeff Zhang is leading the overall effort as
          president of Alibaba Cloud and CTO of the China-based technology giant.
          Despite the momentum, I don’t believe Alibaba Cloud is profitable. We’ll be
          checking the earnings call to learn if such data points are disclosed.
        </div>
      </article>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import EventBus from '@/eventBus';

export default {
  data: () => ({
    file: null,
    title: '',
    downloaded: false,
  }),
  mounted() {
    axios
      .get(`/api/files/${this.$route.params.id}`)
      .then(({ data: file }) => {
        this.file = file;
        this.file.visitCount += 1;
        this.title = `${file.name} - `;
      })
      .catch((err) => {
        console.log(err);

        if (err.response.status === 500) {
          return EventBus.$emit(
            'error',
            'Server Error: Unable to handle the request now. Please try again later.',
          );
        }

        return this.$router.push('/404');
      });
  },
  methods: {
    onFileDownload() {
      this.file.downloadCount += 1;
      this.downloaded = true;

      axios.post(`/api//download/${this.file.encodedName}`);
    },
  },
  metaInfo() {
    return {
      title: this.title,
    };
  },
};
</script>
